import axios from 'axios'
import { defineStore } from 'pinia'
import Swal from 'sweetalert2'

import debounce from 'lodash/debounce'
import statusStore from './statusStore'
const status = statusStore()
export default defineStore('cartStore', {
  state: () => ({
    cartData: {
      carts: [],
      final_total: ''
    },
    coupon_code: ''
  }),
  actions: {
    getCartList () {
      status.isBounced = true
      axios.get(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`)
        .then((res) => {
          this.cartData = res.data.data
          status.isBounced = false
        }).catch(() => {
          status.pushMsg(false, '', '')
        })
    },
    updateCart (item, qty = 1) {
      if (item.qty <= 1) {
        item.qty = 1
      } else if (item.qty >= item.product.inventory) {
        item.qty = item.product.inventory
      }
      axios.put(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${item.id}`, {
        data: {
          product_id: item.product.id,
          qty: Number(item.qty)
        }
      })
        .then((res) => {
          this.getCartList()
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        }).catch(() => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        })
    },
    delCartItem (item) {
      axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart/${item.id}`)
        .then((res) => {
          this.getCartList()
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        }).catch(() => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        })
    },
    delAllCart (item) {
      status.isLoadingItem = item.id
      axios.delete(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/carts`)
        .then((res) => {
          status.isLoadingItem = ''
          this.getCartList()
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        }).catch(() => {
          status.isLoadingItem = ''
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        })
    },
    addToCart: debounce(function (product, qty = 1) {
      status.isLoadingItem = product.id
      let temp = this.cartData.carts.filter(item => item.product_id === product.id)
      temp = { ...temp[0] }
      const resultQty = temp.qty + qty
      if (resultQty > product.inventory) {
        Swal.fire({
          position: 'center',
          icon: 'warning',
          title: ',',
          showConfirmButton: false,
          timer: 2000
        })
        status.isLoadingItem = ''
      } else {
        axios.post(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`, {
          data: {
            product_id: product.id,
            qty
          }
        }).then((res) => {
          this.getCartList()
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
          status.isLoadingItem = ''
        }).catch(() => {
          Swal.fire({
            position: 'center',
            icon: 'error',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
        })
      }
    }, 500),
    addCouponCode () {
      if (this.coupon === '') {
        status.pushMsg(false, '', '')
      } else {
        axios.post(`${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/coupon`, {
          data: { code: this.coupon_code }
        })
          .then((res) => {
            this.getCartList()
            console.log(this.coupon_code)
            console.log(res)
            Swal.fire({
              position: 'center',
              icon: 'success',
              title: '',
              showConfirmButton: false,
              timer: 2000
            })
          }).catch(() => {
            Swal.fire({
              position: 'center',
              icon: 'error',
              title: '',
              showConfirmButton: false,
              timer: 2000
            })
          })
      }
    },
    copyCouponCode (text) {
      navigator.clipboard.writeText(text)
        .then(() => {
          Swal.fire({
            position: 'center',
            icon: 'success',
            title: '',
            showConfirmButton: false,
            timer: 2000
          })
          this.coupon_code = text
        })
    }
  }
})
